/* Max-width: 800px ======== */

@media (max-width: 800px) {

  .header__header {
    overflow:visible;
  }

  .header__burger {
    margin-left: auto;
    margin-right: 20px;
    cursor: pointer;
    padding: 5px;
    width: 25px;
    height: 25px;
    
  }

  .header__btn {
    display: block;
    
    /* position: absolute; */
    width: 25px;
    height: 3px;
    /* right: 20px; */
    background-color: #fff;
    /* z-index: 5; */
    position: relative;
  }

  .header__btn:after,
  .header__btn::before {
    content: '';
    display: block;
    width: 100%;
    background-color: #fff;
    height: 3px;
    position: absolute;
  }

  .header__btn:after {
    top: 16px;
  }

  .header__btn::before {
    bottom: -8px;
  }

   .nav {
    display: none;
  }

  .nav.active {
    display: block;
  }

  .nav.active {
    position: absolute;
    width: 100%;
    padding: 20px 0;
    top: 60px;
    text-align: center;
    z-index: 5;
  }

  .nav.active a {
    display: block;
    padding: 10px;
    width: 100%;
    background-color: rgba(0, 0, 0, 1);
  }

  .nav.active a + a {
    border-top: .5px solid #fff;
  }  

  .header__link {
    display: block;
    
  }

  .header__info {
    padding-left: 10px;
    margin: 0 auto;
  }

  .header__up {
    padding-left: 20px;
    margin: 10px auto 0;
  }

  .content {
    min-height: 610px;
  }

  .content__title {
    max-width: 220px;
    max-height: 220px;
    padding: 73px 48px;
  }

  .title__inner {
    font-size: 44px;
  }

  .title__sub {
    font-size: 16px;
  }

  .content__text {
    padding-top: 60px;
    max-width: 630px;
  }

  .brands__inner {
    padding: 40px 0 50px 0;
}

.brands__items {
  padding-top: 25px;
}

.brands__title {
  padding-bottom: 25px;
}

.about__items {
  flex-direction: column;
  align-items: center;
  /* width: 100%; */
  padding: 0 0px;
}

.about__item {
  margin-bottom: 15px;
  text-align: center;
  max-width: 500px;
}
}

